<script>
  import Input from "./ui/Input.svelte";
  import request from "./request";
  export let visitor;
  export let socket;
  export let _chats;
  export let textareaClass;
  export let init;
  let msgInput;

  function reinit () {
    init(true)
  }

  function whisper(message) {
    socket.whisper("message", message);
  }

  function sendMessage() {
    const id = localStorage.getItem("conversation_id");
    const msg = msgInput;
    if (id) {
      request({
        url: `api/conversation/${id}/send-message`,
        method: "POST",
        data: { type: 1, content: msg },
      }).then(({success, data}) => {
        if (!success) {
          return;
        }

        const message = {
          type: 1,
          content: msg,
          id: parseInt((Math.random()*9999999).toString()).toString(),
          sender_id: visitor.id,
          sender_type: 'App\\Models\\Visitor',
          sender_type_text: 'visitor',
          sender: visitor,
          created_at: (new Date()).toISOString(),
          updated_at: (new Date()).toISOString(),
        };

        whisper(message);
        _chats.push(message);
      });
    }
  }

  $: {
    socket;
  }
</script>

<style>
  div {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
  }

  form.msg-send-form {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    max-width: 640px;
    background-color: white;
    padding: 0.25em 1em;
    border-top: 1px solid #d8dee3;
  }

  .disabled, .disabled form {
    background-color: #eee;
    height: 94px;
    padding: 15px;
    border-top: 1px solid #ccc;
  }

  .disabled .tips {
    font-size: 14px;
    position: relative;
  }
</style>

<div class="{textareaClass}">
  {#if textareaClass !='disabled'}
    <form
      class="msg-send-form"
      autocomplete="off"
      on:submit|preventDefault={(e) => {
        if (!msgInput || !msgInput.trim()) return;
        sendMessage();
        msgInput = '';
        e.target.msg.focus();
      }}>
      <Input
        multiline
        maxRows={3}
        bind:value={msgInput}
        name="msg"
        placeholder="ËæìÂÖ•ÊÇ®ÁöÑÊ∂àÊÅØ"
        ariaLabel="ËæìÂÖ•ÊÇ®ÁöÑÊ∂àÊÅØ"
        {socket}
        />
    </form>
  {/if}
  {#if textareaClass == 'disabled'}
    <div class="tips">Êú¨Ê¨°ÊúçÂä°Â∑≤ÁªìÊùü, Ëã•ÊÇ®‰ªçÊúâÈúÄË¶ÅÂí®ËØ¢ÁöÑÈóÆÈ¢ò, Ê¨¢ËøéÂÜçÊ¨°<span role="link" on:click={(e) => reinit()}>ÂèëËµ∑Âí®ËØ¢</span>.</div>
  {/if}
</div>
